import { useMemo, useState } from "react"
import { ActionsMenu, RPTooltip } from "@rp/react/web/common/base-components"
import { Actions } from "@rp/react/web/common/types"
import { Box, IconButton } from "@rp/react/web/mui/core"
import { Delete, HelpOutline } from "@rp/react/web/mui/icons"
import { WatchlistDataTabsTableEntity } from "../WatchlistDataTabsTable.types"

function WatchlistDataTableActionsCell({ row }: { row: WatchlistDataTabsTableEntity }) {
  const [isOpen, setOpen] = useState(false)
  const actions = useMemo(() => {
    const remove: Actions[0] = {
      id: row.id,
      label: "Remove",
      icon: <Delete />,
      onClick: () => {
        row.removeEntity?.(row.id)
        setOpen(false)
      },
    }
    return [remove]
  }, [row])

  return (
    <Box
      className="hover-action"
      sx={{
        display: "flex",
        justifyContent: "flex-end",
        opacity: isOpen ? 1 : 0,
        transition: "opacity 0.2s",
        padding: 0,
      }}
    >
      {row.entityMotivation && (
        <RPTooltip title="Why is this company in the watchlist?">
          <IconButton
            size="small"
            onClick={event => {
              event.stopPropagation()
              row.onMotivationClicked?.(row)
            }}
          >
            <HelpOutline />
          </IconButton>
        </RPTooltip>
      )}
      {row.canEdit && (
        <ActionsMenu
          open={isOpen}
          setOpen={setOpen}
          actions={actions}
          item={row.id}
          anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
          transformOrigin={{ horizontal: "right", vertical: "top" }}
        />
      )}
    </Box>
  )
}

export { WatchlistDataTableActionsCell }
